/* Materials variant */
.materials {
  position: relative;
  padding: 50px 0 75px 0;
  background: white;
  &__carousel {
    overflow: visible; }
  &__item {
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    cursor: pointer;

    &-info {
      flex: 1 1 auto;
      display: flex;
      flex-direction: column;
      padding-top: 20px;
      // margin-left: -10px
      // margin-right: -10px
      background: white;
      top: 0;
      left: 10px;
      right: 10px;
      pointer-events: none;
      &::before {
        display: inline-block;
        content: ' ';
        position: absolute;
        top: 0;
        bottom: 0;
        left: -7%;
        right: -7%;
        z-index: 1;
        background: white;
        opacity: 0;
        pointer-events: none;
        border: 6px solid transparent;
        transition: .25s $ease; } }

    &-icon {
      position: relative;
      z-index: 2;
      pointer-events: none;
      user-select: none;
      width: 100%;
      font-size: 0;
      margin-bottom: 7px;
      & + p {
        flex: 1 1 auto;
        position: relative;
        z-index: 2;
        min-height: 48px;
        line-height: 1.5;
        font-weight: 600; }
      img {
        max-width: 100%; } }

    &-description {
      position: relative;
      z-index: 2;
      margin-top: auto;
      font-size: 14px;
      line-height: 24px;
      opacity: 0;
      height: 0;
      transform: translate(0,20px);
      transition: .25s $ease; }

    &:hover {
      z-index: 2;
      .materials__item-info {
        position: absolute;
        pointer-events: all;
        &::before {
          opacity: 1;
          border-color: $grayColor; } }

      .materials__item-description {
        opacity: 1;
        transform: translate(0,0px);
        height: auto; } } } }


.materials__arrow {
  display: none;
  position: absolute;
  right: 30px; }

@include r($desktop) {
  .materials {
    padding-bottom: 50px;
    &__item-info {
      border-width: 0; }
    &__item:hover {
      .materials__item-info {
        position: relative;
        left: 0;
        right: 0;
        // width: 100%
        // left: auto
        &::before {
          border-color: transparent; } } }

    &__item-description {
      opacity: 1;
      height: auto;
      transform: translate(0,0); } } }

@include r($tablet) {
  .materials {} }



@include r($mobile) {
  .materials {
    padding-top: 20px;
    .swiper-wrapper {
      flex-direction: column; } }

  .materials__arrow {
    display: inline-block; }

  .materials__item {
    border-top: 1px solid $borderInput;
    &:hover {
      .materials__item-description {
        opacity: 0;
        height: 0; }
      .materials__arrow {
        right: 20px; } }
    .materials__item-description, .materials__item-icon {
      opacity: 0;
      height: 0; }
    .materials__item-info {
      padding-left: 30px;
      padding-right: 30px;
      padding-bottom: 20px;
      &::before {
        display: none; } }
    .materials__item-icon + p {
      min-height: 0;
      margin: 0;
      text-align: left; }
    .materials__item-description {
      .btn-primary {
        width: 100%;
        text-align: center; } } }
  // is-show class for materials__item
  .materials__item.is-show {
    .materials__item-description , .materials__item-icon {
      opacity: 1;
      height: auto; }
    .materials__item-icon + p {
      margin: 20px 0 20px 0; }
    .materials__item-description p {
      text-align: left; }
    .materials__arrow {
      top: 180px;
      transform: rotate(-180deg); } } }

@include r(414) {
  .materials {
    .materials__item {
      margin-left: -30px;
      margin-right: -30px; } } }
