
.swiper-button-prev {
  left: 15px;
  background-image: url('../img/icon-arrow-left.png');
  transform-origin: left center; }

.swiper-button-next {
  right: 15px;
  background-image: url('../img/icon-arrow-right.png');
  transform-origin: right center; }


.why {
  .swiper-button-prev, .swiper-button-next {
    top: calc(25% + 85px);
    @include r($mobile) {
      top: calc(25% + 65px); } } }

.materials {
  .swiper-button-prev, .swiper-button-next {
    top: 55%;
    @include r($desktop) {
      top: 35%; }
    @include r($mobile) {
      display: none; } } }

.steps {
  .swiper-button-prev, .swiper-button-next {
    top: calc(30% + 105px);
    @include r($mobile) {
      top: calc(30% + 85px); } } }

.works {
  .swiper-button-prev, .swiper-button-next {
    top: calc(40% + 85px);
    @include r($mobile) {
      top: calc(40% + 65px); } } }

.reviews {
  .swiper-button-prev, .swiper-button-next {
    top: calc(37.5% + 95px);
    @include r($mobile) {
      top: calc(37.5% + 75px); } } }

.works, .reviews {
  .swiper-button-prev {
    left: -25px; }
  .swiper-button-next {
    right: -25px; } }


.credit {
  .swiper-button-prev, .swiper-button-next {
    display: none;
    top: calc(22% + 180px);
    @include r($tablet) {
      display: block; }
    @include r(640) {
      top: calc(22% + 230px); }
    @include r($mobile) {
      top: calc(22% + 185px); }
    @include r(504) {
      top: calc(22% + 205px); } } }

@include r(1440) {
  .works, .reviews {
    .swiper-button-prev {
      left: 15px; }
    .swiper-button-next {
      right: 15px; } } }


@include r($desktop) {
  .swiper-button-prev, .swiper-button-next {
    transform: scale(0.55); } }

@include r($tablet) {
  .credit {} }

@include r($mobile) {
  .works, .reviews {
    .swiper-button-prev {
      left: 7px; }
    .swiper-button-next {
      right: 7px; } }
  .swiper-button-prev {
    left: 7px; }
  .swiper-button-next {
    right: 7px; } }
