/* Calculate */
.calculate {
  min-height: 640px;
  padding: 60px 0;
  background: url('../img/calculate.jpg') no-repeat center top;
  background-size: cover;

  &__form {
    width: 100%;
    max-width: 450px;
    margin: -12px auto 0 auto;
    color: #fff; } }

.position-relative {
  position: relative; }

.color-white {
  color: #fff; }

.square__number {
  position: absolute;
  top: 25px; }

// Responsive issue for _calculate component
@include r($min-desktop);

@include r($desktop) {
  .calculate {
    min-height: 600px;
    padding: 30px 0;
    background-position: right top; } }

@include r($tablet);

@include r($mobile) {
  .calculate {
    .banner__form-row {
      flex-direction: column; }
    .banner__form-el {
      width: 100%;
      max-width: 100%;
      input.form-el-custom-size {
        width: 100%;
        max-width: 86%; } } } }
