/* Contact */
.contact {
  position: relative;
  z-index: 2;
  // background: $graySecondaryColor
  margin-top: -130px;
  pointer-events: none;
  .container {
 }    // max-height: 180px

  .ico {
    margin-top: 8px;
    color: $colorOrange;
    &-location {
      font-size: 22px; }
    &-phone {
      font-size: 20px; }
    &-mail {
      font-size: 16px; }
    &-time {
      font-size: 19px; } }


  &__info {
    padding: 60px 59px 40px 58px;
    background: #fff;
    display: flex;
    justify-content: space-between;
    align-items: self-start;
    pointer-events: all;
    margin: 0 0px;
    &-col {
      padding-left: 5px;
      padding-right: 5px;
      &:first-child {
        padding-left: 0;
        flex: 0 0 350px;
        max-width: 350px; }
      &:last-child {
        padding-right: 0; } }
    h1 {
      margin-top: 0;
      line-height: 46px; } }

  .contact__el {
    display: flex;
    font-size: 15px;
    line-height: 27px;
    margin-bottom: 20px;
    .ico {
      flex: 0 0 auto;
      &-location {
        margin-right: 5px; } }
    p {
      margin: 0;
      display: inline-block;
      padding-left: 25px;
      vertical-align: top; }

    strong {
      font-size: 17px;
      font-weight: 900; }
    &--phone {
      a {
        transition: .25s $ease;
        &:hover {
          color: $colorOrangeHover; } } }

    &--mail {
      a {
        color: #2f57a3;
        transition: all .25s ease;
        border-bottom: 1px solid #2f57a3;

        &:hover {
          color: $colorOrangeHover;
          border-color: transparent; } } } } }

/* Responsive for _contact component */
@include r(1200) {
  .contact__info-col:first-child {
    max-width: 250px; }
  .footer {
    .header__contact {
      min-width: 180px; } } }

@include r($desktop) {
  .contact {
    .contact__info {
      padding: 30px;
      display: block;
      &-col {
        display: inline-block;
        vertical-align: top;
        &:first-child {
          display: block;
          max-width: none;
          text-align: center; }
        h1 {
          font-size: 32px;
          margin-bottom: 20px; }
        &:last-child {
          float: right; } } } } }

@include r(767) {
  .contact {
    .contact__info {
      padding: 20px; }
    .container {
      max-height: none; }
    .contact__info-col {
      max-width: 400px;
      margin-left: auto;
      margin-right: auto; }
    .contact__info-col, .contact__info-col:last-child {
      display: block;
      float: none;
      padding-left: 0;
      padding-right: 0; }
    .contact__info-col h1 {
      text-align: left;
      font-size: 28px;
      line-height: 38px;
      margin-bottom: 40px; }
    .contact__el p {
      padding-left: 5px; } } }

@include r($mobile) {
  .contact {
    margin-top: 0;
    // padding-top: 40px
    .container {
      padding-left: 0;
      padding-right: 0; }
    .contact__info {
      padding: 40px 30px 20px; }
    .contact__info-col {
      // margin-right: -10px
      p {
        padding-left: 20px; }
      br {
        display: none; }
      h1 {
        margin-bottom: 20px; } } }

  .contact__el {
    font-size: 13px;
    line-height: 25px; } }
