    padding-right: 15px;
    padding-left: 15px;
  }
  .project .project__cta {
    border: none;
  }
  .project .project__cta-title {
    font-size: 32px;
  }
  .project .project__cta-subtitle {
    font-size: 20px;
  }
  .project .project__cta-form {
    display: block;
  }
  .project .project__cta-form input[type='submit'] {
    width: 300px;
  }
}

@media only screen and (max-width: 568px) {
  .project .project__cta {
    font-size: 14px;
  }
  .project .project__cta-title {
    font-size: 28px;
    line-height: 38px;
  }
}

@media only screen and (max-width: 375px) {
  .project {
    padding: 30px 0;
  }
  .project p {
    margin-left: -10px;
    margin-right: -10px;
  }
  .project p br {
    display: none;
  }
  .project .project__cta {
    margin-top: 0;
    margin-bottom: 0;
  }
  .project .project__cta-title {
    margin: 0;
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;
  }
  .project .project__cta-subtitle {
    margin-top: 15px;
  }
  .project .project__cta-form input[type='tel'], .project .project__cta-form input[type='submit'] {
    width: 260px;
  }
}

/* Credit */
.credit {
  position: relative;
  padding: 45px 0 55px 0;
  background: #f6f7f8;
}

.credit h1 {
  margin-bottom: 10px;
}

.credit__subtitle {
  margin-top: 0;
  margin-bottom: 80px;
  font-size: 20px;
  line-height: 30px;
}

.credit .why__item {
  flex: 0 1 16.6%;
}

.credit .why__item-icon {
  margin-bottom: 30px;
}

@media only screen and (max-width: 992px) {
  .credit .swiper-wrapper {
    flex-wrap: wrap;
  }
  .credit .why__item {
    flex: 0 1 33.3%;
    max-width: 100%;
  }
  .credit__subtitle {
    margin-top: -10px;
    margin-bottom: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .credit {
    padding-top: 25px;
  }
  .credit .swiper-wrapper {
    flex-wrap: nowrap;
  }
  .credit .why__item {
    flex: 0 0 auto;
  }
  .credit__subtitle {
    font-size: 18px;
  }
  .credit .wrapper {
    display: block;
  }
  .credit .why__item {
    max-width: none;
    display: inline-block;
    vertical-align: top;
  }
}

@media only screen and (max-width: 568px) {
  .credit {
    padding-bottom: 20px;
  }
}

/* Bank */
.bank {
  padding-top: 20px;
  padding-bottom: 65px;
  background: #f6f7f8;
}

.bank .wrapper {
  margin-left: -12px;
  margin-right: -12px;
}

.bank__title {
  margin: 0 0 35px 0;
  font-size: 26px;
  line-height: 38px;
  font-weight: 600;
}

.bank__item {
  position: relative;
  width: 100%;
  flex: 0 1 16.666%;
  padding-right: 12px;
  padding-left: 12px;
  min-height: 96px;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bank__item::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 12px;
  right: 12px;
  background: #fff;
  z-index: 1;
}

.bank__item .icon {
  position: relative;
  z-index: 2;
}

@media only screen and (max-width: 992px) {
  .bank .wrapper {
    flex-wrap: wrap;
  }
  .bank__item {
    flex: 0 1 33.3%;
  }
}

@media only screen and (max-width: 768px) {
  .bank__item {
    margin-bottom: 20px;
  }
  .bank {
    padding-bottom: 40px;
  }
  .bank__title {
    margin-bottom: 25px;
  }
}

@media only screen and (max-width: 568px) {
  .bank {
    font-size: 0;
    padding-top: 10px;
    padding-bottom: 25px;
  }
  .bank__item {
    flex: 0 1 50%;
    padding-left: 5px;
    padding-right: 5px;
    overflow: hidden;
    margin-bottom: 10px;
    min-height: 75px;
  }
  .bank__item::after {
    left: 5px;
    right: 5px;
  }
  .bank__item i {
    transform: scale3d(0.75, 0.75, 0.75);
  }
}

/* Map */
#map {
  min-height: 432px;
}

@media only screen and (max-width: 568px) {
  #map {
    min-height: 350px;
  }
}

@media only screen and (max-width: 375px) {
  #map {
    min-height: 300px;
  }
}

/* Contact */
.contact {
  position: relative;
  z-index: 2;
  margin-top: -130px;
  pointer-events: none;
}

.contact .ico {
  margin-top: 8px;
  color: #eaac39;
}

.contact .ico-location {
  font-size: 22px;
}

.contact .ico-phone {
  font-size: 20px;
}

.contact .ico-mail {
  font-size: 16px;
}

.contact .ico-time {
  font-size: 19px;
}

.contact__info {
  padding: 60px 59px 40px 58px;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: self-start;
  pointer-events: all;
  margin: 0 0px;
}

.contact__info-col {
  padding-left: 5px;
  padding-right: 5px;
}

.contact__info-col:first-child {
  padding-left: 0;
  flex: 0 0 350px;
  max-width: 350px;
}

.contact__info-col:last-child {
  padding-right: 0;
}

.contact__info h1 {
  margin-top: 0;
  line-height: 46px;
}

.contact .contact__el {
  display: flex;
  font-size: 15px;
  line-height: 27px;
  margin-bottom: 20px;
}

.contact .contact__el .ico {
  flex: 0 0 auto;
}

.contact .contact__el .ico-location {
  margin-right: 5px;
}

.contact .contact__el p {
  margin: 0;
  display: inline-block;
  padding-left: 25px;
  vertical-align: top;
}

.contact .contact__el strong {
  font-size: 17px;
  font-weight: 900;
}

.contact .contact__el--phone a {
  transition: 0.25s ease-in-out;
}

.contact .contact__el--phone a:hover {
  color: #efa017;
}

.contact .contact__el--mail a {
  color: #2f57a3;
  transition: all .25s ease;
  border-bottom: 1px solid #2f57a3;
}

.contact .contact__el--mail a:hover {
  color: #efa017;
  border-color: transparent;
}

/* Responsive for _contact component */
@media only screen and (max-width: 1200px) {
  .contact__info-col:first-child {
    max-width: 250px;
  }
  .footer .header__contact {
    min-width: 180px;
  }
}

@media only screen and (max-width: 992px) {
  .contact .contact__info {
    padding: 30px;
    display: block;
  }
  .contact .contact__info-col {
    display: inline-block;
    vertical-align: top;
  }
  .contact .contact__info-col:first-child {
    display: block;
    max-width: none;
    text-align: center;
  }
  .contact .contact__info-col h1 {
    font-size: 32px;
    margin-bottom: 20px;
  }
  .contact .contact__info-col:last-child {
    float: right;
  }
}

@media only screen and (max-width: 767px) {
  .contact .contact__info {
    padding: 20px;
  }
  .contact .container {
    max-height: none;
  }
  .contact .contact__info-col {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
  .contact .contact__info-col, .contact .contact__info-col:last-child {
    display: block;
    float: none;
    padding-left: 0;
    padding-right: 0;
  }
  .contact .contact__info-col h1 {
    text-align: left;
    font-size: 28px;
    line-height: 38px;
    margin-bottom: 40px;
  }
  .contact .contact__el p {
    padding-left: 5px;
  }
}

@media only screen and (max-width: 568px) {
  .contact {
    margin-top: 0;
  }
  .contact .container {
    padding-left: 0;
    padding-right: 0;
  }
  .contact .contact__info {
    padding: 40px 30px 20px;
  }
  .contact .contact__info-col p {
    padding-left: 20px;
  }
  .contact .contact__info-col br {
    display: none;
  }
  .contact .contact__info-col h1 {
    margin-bottom: 20px;
  }
  .contact__el {
    font-size: 13px;
    line-height: 25px;
  }
}
